<template>
  <li @click.stop=" selClick(model)"  :class="{hasBackItem:!onBrother && isFolder && !isLastLeaf}">
    {{isOpenNode(model, true)}}
    <div class="rowPanle">
      <span v-if="!(isInit && onBrother)"  :class="{itemBrotherLeaf: !isLastLeaf && !onBrother, itemLastLeaf: !isInit }"></span>
      <div class="folder-panel" v-if="isFolder"
           :class="isFolder?'':' unfolder-width'"
           @click.stop="toggle(model)">
        <span  class="folder-style">{{open ? '-' : '+' }}</span>
      </div>
      <i v-if="isFolder" class="hasIcon" style=" margin: 0.1em 0.5em 0em 0.5em ;"><icon :name="open ? 'folder-open': 'folder'" scale="1.2"></icon></i>
      <i v-if="!isFolder" class="hasIcon" style=" margin: 0.1em 0.5em 0em 0.5em ;"><icon name="file-text" scale="1.2"></icon></i>
      <div class="liItem" :title="model[textKey]" :class="isSelectNode(model) ? 'liItemSel':''" >{{ model[textKey] }}</div>
    </div>
    <ul v-show="open" v-if="isFolder" style="margin-left:1.7em;" >
      <template  v-for="(modelChild, $index) in model.children">
          <item @selClicks="selClick"
            class="item"
            :textKey = "textKey"
            :valueKey = "valueKey"
            :isLastLeaf = "($index === model.children.length - 1  && model.children.length > 1) || (model.children.length == 1 && $index == 0)"
            :onBrother = "model.children.length === 1 "
            :noCheck="noCheck"
            :model="modelChild">
         </item>
      </template>
    </ul>
  </li>
</template>
<script>
  let openNode = [];
  import 'vue-awesome/icons/folder-open'
  import 'vue-awesome/icons/folder'
  import 'vue-awesome/icons/file-text'
  export default {
    name: 'item',
    component: {},
    props: {
      noCheck: {

      },
      isInit: {
       
      },
      model: {
      },
      textKey: {
        default: 'orgName'
      },
      isLastLeaf: {

      },
      onBrother: {
        default: true
      },
      valueKey: {
        default: 'orgNo'
      }
    },
    data: function () {
      return {
        sel: false,
        open: false,
        selId:0,
      }
    },
    computed: {
      isFolder: function () {
        return this.model.children && this.model.children.length;
      },
    },
    methods: {
      isLast: function () {
         for(let i = 0;i < this.model.children.length; i++) {
           if(this.model.children[i].children && this.model.children[i].children.length > 0) {
             return true
           }
         }
         return false
      },
      isSelectNode: function (val, compareVal) {
         let compareObj = compareVal || this.$store.state.currentSelectOrg;
         let sign = true;
         if (typeof this.valueKey === 'string') {
            sign = sign && (compareObj[this.valueKey] === val[this.valueKey]);
         } else if  (this.valueKey instanceof Array) {
           for(let i=0; i<this.valueKey.length; i++) {
              let item = this.valueKey[i];
              sign = sign && (compareObj[item] === val[item])
           }
         }

          return sign
      },
      toggle: function (val) {
        if (this.isFolder) {
          this.open = !this.open;

        }
        this.isOpenNode(this.getSaveNode(val));
      },
      isOpenNode: function (val, openFlag) {
        if(openNode.length === 0) {
          if(openFlag) {
            this.open = false;
          } else {
            openNode.push(val)
          } 
         return 
        } else {
           for(let i=0; i<openNode.length; i++) {
             if(this.isSelectNode(val,openNode[i])) {
                if(openFlag) {
                   this.open = true;
                } else {
                   openNode.splice(i, 1);
                }
                return 
              } else if(i === openNode.length -1) {
                 if(openFlag) {
                    this.open = false;
                 } else {
                    openNode.push(val);
                }
                 return 
             }
          } 
        }
      },
      getSaveNode: function (val) {
        let current = {};
        for(let item in val) {
          if(item !== 'children') {
            current[item] = val[item];
          }
        }
        return current
      },
      //选中的数据
      selClick: function (val) {
        
        let obj = this.getSaveNode(val);
        this.$store.state.currentSelectOrg = obj;
        
        // this.$emit('selCheck', this.selectors);
        if (!this.sel) {
          this.$emit('selClicks', obj)
        } else {
          this.$emit('selClicks', '')
        }
      },
    },
    created: function () {
      this.isOpenNode(this.model, true);
      if (this.isInit) {
        this.selClick(this.model);
      }
    },
    watch: {
     model: {
       handler(val) {
        }
     }
    }
  };
</script>
<style lang="scss">

  .item {
      font-size: 1em;
      width: 100%;
      padding:0em 0 ;
      position:relative;
      //background: url(../../assets/img/point.gif)  0.08% 0 repeat-y;

  }

  .rowPanle {
    padding: 0.2em 0;
    .folder-panel {
      display: inline-block;
      cursor: pointer;
      float: left;
      width: 1.5em;
      height: 1.5em;
      margin-top:-0.15em;
      background-color: #227fdf;
      border-radius: 0.2em;
      text-align: center;
      .folder-style {
        font-size: 1.3em;
      }
      .icon.folder {
        background-image: url(../../assets/img/f2.png);
      }
      .icon.folder-open {
        background-image: url(../../assets/img/f3.png);
      }
      .icon.file-text {
        background-image: url(../../assets/img/f.png);
      }
    }
    .unfolder-width {
      background-color: transparent
    }
    .liItem {
      float: left;
      display: inline-block;
      cursor:pointer;
     // padding-right: 1em;
      width: calc(100% - 6em);
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
      text-align:left
    }
    .liItem:hover{
      //background-color: #227fdf;
    }
    .liItemSel{
      color:#D38642;
    }
  }
  .rowPanle:after{
    content: '';
    display: block;
    width:0px;
    height:1px;
    clear:both
  }
  .hasBackItem:before{
  
    //background: url(../../assets/img/point.gif);
   
    content: '';
    display: block;
    width: 1px;
    height: calc(100% - 2em);
   // background: url('../../assets/img/metro.png');
    background: url(../../assets/img/point.png);
    background-position: -94px 34%;
    background-position-x: 100%;
    background-repeat-y: repeat;
    top: 24px;
    position: absolute;
    left: 9px;
    background-size: initial;
  }
  .itemLeaf{
    .rowPanle{
      padding-left:1.3em;

    }
  }
  .itemLastLeaf{
    background: url('../../assets/img/metro.png');
    background-position: -94px -32px;
    display: inline-table;
    float:left;
    width:1em;
    height: 30px;
    margin-left: 9px;
     background-size: auto;
    margin-top: -10px;
  }
  .itemBrotherLeaf{
    background-position: -94px -11px;
    height: 35px;
  }


</style>



