<template>
  <div class="info-container " style="height: calc(100% - 0em);margin: 0;padding:0;width:100%">
    <div class="info-div" style="height:100%">
      <div class="list-content  con-left leftMenu" >
        <div class="list-header">
          <i class="hasIcon hasImg">
            <img src="dist/img/configuration/move-button.png"/>
          </i>
          <label>部门管理</label>
        </div>
        <div class="treeStyle">
          <ul>
            <template v-for="(model, $index) in treeData"> 
                <items @selClicks="selNodeClick" class="item"  :model="model" :isInit="true"></items>
            </template>
          </ul>
        </div>
        <div class="optStyle">
          <div class="org-style" @click="addChild" v-if="controls[1]">
            <i class="hasIcon hasImg">
              <img src="dist/img/configuration/add-new-button.png">
            </i>
             新增部门
          </div>
          <div class="org-style" @click="delOrg" v-if="controls[3]">
            <i class="hasIcon hasImg">
              <img src="dist/img/configuration/delete-button.png">
            </i>
            删除部门
          </div>
        </div>
      </div>
      <div id="permiss_opt" class="list-content con_center" style="overflow: hidden;width:calc(80% - 3.5em)">
        <div >
          <div class="list-header">
            <i class="hasIcon hasImg">
              <img src="dist/img/face/sh.png"/>
            </i>
            部门编辑
          </div>
          <div style="padding-top:20px;width:70%;font-size:0.875em">
            <ul class="dialog-ul" >
              <li v-for="(item,$index) in orgEditData" class=" formList " :class="item.classes">
                <label v-if="!item.isEmpty" class="label-show"><span class="emptyTips"> * </span>
                  {{item.label}} </label>
                <label v-if="item.isEmpty" class="label-show"><span>&nbsp;</span>{{item.label}} </label>
                <inputs v-if="item.type === 'input'" :isEmpty="item.isEmpty" :option="item.option"
                        @getInput="getInputFrom"
                        :ref="item.option.key"></inputs>
                <selects v-if="item.type === 'select'" :option="item.option" @getSelect="getSelectsFrom"
                         :ref="item.option.key"></selects>
              </li>
            </ul>
          </div>
          <div style="margin-top:20px;font-size:0.875em" v-if="controls[2]">
            <button class="button-default" @click="saveEdit">保存</button>
            <button style="background-color:#F4A460" class="button-default cancel-button" @click="cancles">取消</button>
          </div>

        </div>


      </div>
    </div>
    <dialogs type="customer" ref="editComponyInfo" width="35em" v-if="showAddInfo">
      <div slot="header">
        添加部门
      </div>
      <div slot="content" >
        <ul class="dialog-ul">
          <li v-for="(item,$index) in orgAddData" class=" formList " :class="item.classes">
            <label v-if="!item.isEmpty" class="label-show"><span class="emptyTips"> * </span> {{item.label}} </label>
            <label v-if="item.isEmpty" class="label-show"><span>&nbsp;</span>{{item.label}} </label>
            <inputs v-if="item.type === 'input'" :isEmpty="item.isEmpty" :option="item.option" @getInput="getInputFrom"
                    :ref="item.option.key"></inputs>
            <selects v-if="item.type === 'select'" :option="item.option" @getSelect="getSelectsFrom"
                     :ref="item.option.key"></selects>
          </li>
        </ul>
      </div>
      <div slot="footer">
        <button class="button-default" @click="saveAdd">提交</button>
        <button class="button-default cancel-button" @click="closeChangeInfo">关闭</button>
      </div>
    </dialogs>
    <dialogs type="alert" ref="alerts"></dialogs>
    <dialogs type="confirm" ref="confirms"></dialogs>
  </div>
</template>

<script>
  import items from '../../childComponent/treeItem.vue'

  export default {
    data: function () {
      return {
        controls: [],
        selector: [],
        treeData: [],
        orgInfosSel: {
          orgNo: 0,
          orgName: '',
          pOrgNo: "",
        },
        orgInfosAdd: {
          orgNo: 0,
          orgName: '',
          pOrgNo: "",
        },
        copySel: {
          orgNo: 0,
          orgName: '',
          pOrgNo: "",
        },
        showAddInfo: true,
      }
    },
    components: {
      items
    },
    computed: {
      orgEditData: function () {
        let width = 'calc(50% - 4.8em)';
        return [
          {
            label: '上级部门',
            classes: 'halfWidth',
            isEmpty: false,
            type: 'select',
            option: {
              type: 'complexSelect',
              key: 'orgInfosSel.pOrgNo',
              hasParent:true,
              cs: 'server',
              url: 'org/queryOrgTreeInfo',
              method: 'post',
              width: '20em',
          //    constant: {'orgNo': this.orgInfosSel.pOrgNo},
              text: 'orgName',
              value: 'orgNo',
              rowName: 'data'
            }
          },
          {
            label: '部门名称',
            classes: 'halfWidth hasMarginLi',
            isEmpty: false,
            type: 'input',
            option: {
              type: 'normalInput',
              key: 'orgInfosSel.orgName',
              value: this.orgInfosSel.orgName,
              width: '20em',
              regular: '',
              length: 50,
              placeHolder: '请输入部门名称',
              errorMsg: '任意字符'
            }
          }
        ];
      },
      orgAddData: function () {
        let width = 'calc(100% - 6.5em)';
        return [
          {
            label: '上级部门',
            classes: 'allWidth ',
            isEmpty: false,
            type: 'select',
            option: {
              type: 'complexSelect',
              key: 'orgInfosAdd.pOrgNo',
             // cs: 'server',
             // url: 'org/queryOrgTreeInfo',
             // method: 'post',
              width: width,
              cs: 'client',
              data: this.treeData,
              text: 'orgName',
              value: 'orgNo',
              rowName: 'data'
            }
          },
          {
            label: '部门名称',
            classes: 'allWidth',
            isEmpty: false,
            type: 'input',
            option: {
              type: 'normalInput',
              key: 'orgInfosAdd.orgName',
              value: '',
              width: width,
              regular: '',
              length: 50,
              placeHolder: '请输入部门名称',
              errorMsg: '任意字符'
            }
          },

        ]
      }
    },
    methods: {
      alerts: function () {
        return this.$refs.alerts
      },
      getInputFrom: function (param) {
        this.getOption(param, this);

      },
      getSelectsFrom: function (val) {
        this.getOption(val, this);
      },
      //
      cancles: function () {
        for(let key in this.copySel) {
          this.orgInfosSel[key] = this.copySel[key];
        }
      },
      saveEdit: function () {
        let that = this;
        that.regularTest({
          prefix: "orgInfosSel",
          emptyKey: ["orgName"],
          callback: function () {
            that.getRequest({
              url: 'org/edit',
              param: that.orgInfosSel,
              method: 'post',
              success: function (data) {
                that.alerts().show({
                  msg: data.message
                });
                that.init();
                that.reBindSelect();
                for(let key in that.orgInfosSel) {
                    that.copySel[key] = that.orgInfosSel[key];
                }
              },
              error: function (data) {
                that.alerts().show({
                  msg: data.message
                });
              }
            }, true);
          }
        });

      },
      //提交添加
      saveAdd: function () {
        let that = this;
        that.regularTest({
          prefix: "orgInfosAdd",
          emptyKey: ["orgName"],
          callback: function () {
            that.getRequest({
              url: 'org/add',
              param: that.orgInfosAdd,
              method: 'post',
              success: function (data) {
                that.alerts().show({
                  msg: data.message
                });
                if (data.code == 1) {
                  that.init();
                  that.reBindSelect();
                }

              },
              error: function (data) {
                that.alerts().show({
                  msg: data.message
                });
              }
            }, true);
          }
        });
      },
      reBindSelect:function(){
        this.$refs["orgInfosSel.pOrgNo"][0].initSelect();
        this.$refs["orgInfosAdd.pOrgNo"][0].initSelect();
      },
      closeChangeInfo: function () {
        this.orgInfosAdd.orgName ="";
        this.orgInfosAdd.orgNo =0;

        this.$refs.editComponyInfo.close();
        this.showAddInfo = false;
      },
      //打开新增窗口
      addChild: function (pid) {
        var that = this;
        that.showAddInfo = true;
        that.$nextTick(function () {
          that.$refs.editComponyInfo.show();
        });
      },
      //选择节点数据
      selNodeClick: function (node) {
       // node.chk = true;

        this.orgInfosSel.orgName = this.copySel.orgName = node.orgName;
        this.orgInfosSel.orgNo = this.copySel.orgNo = node.orgNo;
        this.orgInfosSel.pOrgNo = this.copySel.pOrgNo = node.pOrgNo;
        //alert(node.pOrgNo);
        this.$refs["orgInfosSel.pOrgNo"][0].resetDefault(node.pOrgNo);
      },
      //删除组织结构
      delOrg: function () {

        let that = this;
        if (that.orgInfosSel && that.orgInfosSel.orgNo != 0) {
          that.$refs.confirms.show({
            msg: '确定删除选择的部门[' + that.orgInfosSel.orgName + ']？',
            action: {
              confirm: function () {
                that.getRequest({
                  method: 'post',
                  url: 'org/delete',
                  param: that.orgInfosSel,
                  success: function (data) {
                    that.alerts().show({
                      msg: data.message
                    });

                    if (data.code == 1) {
                      that.init();
                      that.orgInfosSel.orgNo = 0; //清空
                      that.orgInfosSel.orgName = ""; //清空
                      that.reBindSelect();
                    }
                  },
                  error: function (data) {
                    that.alerts().show({
                      msg: data.message
                    });
                  }
                }, true)
              }
            }
          });
        }
        else {
          that.alerts().show({
            msg: "请选择删除的部门"
          });
        }

      },
      //初始化页面数据
      init: function () {
        let that = this;
        that.getRequest({
          method: 'post',
          url: 'org/queryOrgTreeInfo',
          success: function (dataSource) {
            that.treeData = [];
            that.treeData = that.treeData.concat(dataSource.data);
          }
        }, true)
      }
    },
    created: function () {
      this.isIn();
      this.getAction();
    }
  }
</script>
<style lang="scss">
  .treeStyle {
    height: calc(100% - 11em);
    text-align: left;
    margin-left: 1em;
    font-size:0.875em;
    input {
      height: 1.2em;
      zoom: 150%;
      vertical-align: middle;
    }
    li {
      font-size: 1em;
      cursor: pointer;
      width: calc(100% - 1.5em);
      padding-left: 0.2em;
    }
    li:after {
      background-color: transparent;
      height: 0
    }
    li:hover, .rowselect {
      background-color: transparent
    }
    .hasBackItem:before{
      left: 12px;
    }
  }

  .leftMenu {
    overflow-x: auto;
    overflow-y: hidden;
    label {
      font-size: 1em;
    }
  }

  .optStyle {
    height: 8em;
    .org-style {
      background-color: #227fdf;
      width: 6em;
      font-size:0.9em;
      height: 1.8em;
      margin: 0 auto;
      line-height: 1.8em;
      margin-bottom: 1.2em;
      padding: 0.4em 2.6em;
      cursor: pointer;
      img {
        margin-right: 0.4em;
        float: left;
        margin-top: 0.33em;
        width: 1.05em;
        height: 1.05em;
      }
      span {
        float: left;
      }
    }
    .org-style:hover {
      background-color: rgba(#227fdf, 0.8);
    }
  }
</style>



